.simple {
  width: 79vw;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}

.simple__menu {
  width: 50%;
  height: 25px;
  border: 2px solid black;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}

.simple_title {
  font-size: calc(1vw + 10px);
  font-weight: 700;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

.simple__c,
.simple__a,
.simple__b,
.simple__d,
.simple__rrr,
.simple__g {
  width: 80%;
  padding: 5px 1px 10px 1px;
  margin-top: 20px;
  border: 5px solid black;
}

.simple__c {
  border-color: red;
}

.simple__a {
  border-color: yellow;
}

.simple__b {
  border-color: green;
}

.simple__d {
  border-color: darkblue;
}

.simple__rrr {
  border-color: violet;
}

.simple__g {
  border-color: black;
}

.simple__answer {
  width: calc(100% - 10px);
  margin-top: 5px;
  padding: 2px;
  border: 3px solid tan;
  box-shadow: inset 0px 0px 2px 3px rgba(255, 217, 0, 0.615);
}

.simple__question {
  width: calc(100% - 10px);
  margin-top: 5px;
  padding: 2px;
  border: 3px solid slategray;
}
